@import url('https://fonts.googleapis.com/css2?family=Exo+2&family=Rampart+One&display=swap');


body {
  background: rgb(16, 16, 16);
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.app {
  display: flex;
  justify-content: center;
  align-items: center;
}

.appMobile {
  display: none;
}

.nameTitle {
  display: flex;
  position: absolute;
  bottom: 0;
}

.firstName {
  margin-left: 2vw;
  margin-right: 15vw;
}

.lastName {
  margin-left: 15vw;
  margin-right: 7vw;
}

.sticker {
  display: none;
}

.homeTitle {
  font-family: 'Rampart One', cursive;
  font-size: 7vw;
  color: rgb(253, 232, 237);
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: flex-end;
  margin: 0;
}


h2 {
  color: rgb(253, 232, 237);
  font-family: 'Exo 2', sans-serif;
  font-size: 55px;
}

.contact {
  position: absolute;
  bottom: 24vw;
  left: 15vw;
}

.portfolio {
  position: absolute;
  bottom: 40vw;
  left:35vw;
}

.cv {
  color: rgb(253, 232, 237);
  font-family: 'Exo 2', sans-serif;
  font-size: 55px;
  font-weight: bold;
  position: absolute;
  bottom: 37vw;
  right:34vw;
}

.qui {
  position: absolute;
  bottom: 24vw;
  right:15vw;
}

.name {
  position: absolute;
  text-align: center;
  margin-right: 4rem;
  top: 18rem;
}

.eye {
  border-radius: 50%;
  height: 50px;
  width: 14rem;
}

.eye:first-child {
  margin-right: 0;
  margin-left: 7rem;
}

.eye .pupil {
  border-radius: 50%;
  object-fit: contain;
  width: 1.6vw;
  transform: translate(-50%, -50%) scale(3);
  transition: transform 0.2s ease-in-out;
}

.row {
  bottom: 13rem;
  position: absolute;
  z-index: 2;
  display: flex;
  transform: scale(0.48) rotate(-5deg);
  opacity: 1;
}

.head {
  z-index: 3;
  position: absolute;
  bottom: 0;
  width: 30%;
}

.mask {
  z-index: 1;
  position: absolute;
  bottom: 15vw;
  width: 15%;
  display: flex;
  align-items: center;
  margin: 0 auto;
}

.arrow1 {
  position: absolute;
  top: 7vw;
  left: 35vw;
  transform: scale(0.60) rotate(200deg);
}

.arrow2 {
  position: absolute;
  top: 23vw;
  left: 26vw;
  transform: scale(0.80)  rotate(15deg);
}

.arrow3 {
  position: absolute;
  top: 20vw;
  right: 30vw;
  transform: scale(0.70)  rotate(50deg);
}

.arrow4 {
  position: absolute;
  top: 16vw;
  right: 30vw;
  transform: scale(0.55)  rotate(310deg);
}


.SMN_effect-65 a span:hover:before {
	opacity: 0.9;
}

@-webkit-keyframes rodarodajequiti {
	from {
		-webkit-transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
	}
}

@-moz-keyframes rodarodajequiti {
	from {
		-moz-transform: rotate(0);
	}
	to {
		-moz-transform: rotate(360deg);
	}
}

@keyframes rodarodajequiti {
	from {
		-webkit-transform: rotate(0);
		-moz-transform: rotate(0);
		-ms-transform: rotate(0);
		-o-transform: rotate(0);
		transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.SMN_effect-65 a span:before {
	content: '';
	position: absolute !important;
	width: 120px;
	height: 120px;
	top: 0;
	bottom: 0;
	margin: auto;
	opacity: 0;
	-webkit-animation: rodarodajequiti 50s infinite linear;
	-moz-animation: rodarodajequiti 50s infinite linear;
	animation: rodarodajequiti 50s infinite linear;
	-webkit-transition: all 1s cubic-bezier(0.17, 0.67, 0.48, 1.32);
	-moz-transition: all 1s cubic-bezier(0.17, 0.67, 0.48, 1.32);
	transition: all 1s cubic-bezier(0.17, 0.67, 0.48, 1.32);
	background-image: url('data:image/png;base64,');
}


.SMN_effect-65 h2:hover:before {
	opacity: 0.9;
}

@-webkit-keyframes rodarodajequiti {
	from {
		-webkit-transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
	}
}

@-moz-keyframes rodarodajequiti {
	from {
		-moz-transform: rotate(0);
	}
	to {
		-moz-transform: rotate(360deg);
	}
}

@keyframes rodarodajequiti {
	from {
		-webkit-transform: rotate(0);
		-moz-transform: rotate(0);
		-ms-transform: rotate(0);
		-o-transform: rotate(0);
		transform: rotate(0);
	}
	to {
		-webkit-transform: rotate(360deg);
		-moz-transform: rotate(360deg);
		-ms-transform: rotate(360deg);
		-o-transform: rotate(360deg);
		transform: rotate(360deg);
	}
}

.SMN_effect-65 h2:before {
	content: '';
	position: absolute !important;
	width: 120px;
	height: 120px;
	top: 0;
	left: 0;
	bottom: 0;
	margin: auto;
	opacity: 0;
	-webkit-animation: rodarodajequiti 50s infinite linear;
	-moz-animation: rodarodajequiti 50s infinite linear;
	animation: rodarodajequiti 50s infinite linear;
	-webkit-transition: all 1s cubic-bezier(0.17, 0.67, 0.48, 1.32);
	-moz-transition: all 1s cubic-bezier(0.17, 0.67, 0.48, 1.32);
	transition: all 1s cubic-bezier(0.17, 0.67, 0.48, 1.32);
	background-image: url('data:image/png;base64,');
}


/* portable */

@media (max-width: 1270px) {
  body {
    width: auto;
  }

  .app {
    display: none;
  }

  .appMobile {
    display: block;
  }

  .nameTitle {
    bottom: auto;
    border-bottom: rgb(253, 232, 237) solid 2px;
    width: 96%;
  }

  .mobileBlock {
    display: block;
    position: relative;
    top:40vw;
    text-align: center;
  }

  a {
    text-decoration: none;
  }
  
  .contact {
    position: relative;
    bottom: 0;
    left: 0;
  }
  
  .portfolio {
    position: relative;
    bottom: 0;
    left: 0;
  }
  
  .cv {
    position: relative;
    bottom: 0;
    left: 0;
  }
  
  .qui {
    position: relative;
    bottom: 0;
    left: 0;
  }

  .arrow1 {
    display: none;
  }

  .arrow2 {
    display: none;
  }

  .arrow3 {
    display: none;
  }

  .arrow4 {
    display: none;
  }

  .mask {
    display: none;
  }

  .head {
    display: none;
  }

  .row {
    display: none;
  }

  .mobileEye {
    display: block;
  }

  .sticker {
    display: block;
    width: 35%;
  }

  .lastName {
    margin: 0 1rem 0 0;
  }

  .firstName {
    margin: 0;
  }

  .contactTitle {
    font-size: 7vw;
  }

  .contactUl {
    padding: 0;
  }

  .aContact {
    width: auto;
    height: auto;
    border-radius: 5%;
  }


}